$templateCache.put("js/states/info/info.html","<div class=\"info-page\"><div class=\"container info-page-intro text-center band band-hero\"><div class=\"row\"><div class=\"col-sm-12\"><insights-logo></insights-logo></div><div class=\"col-sm-8 col-sm-offset-2\"><insights-tagline></insights-tagline></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-10 vcenter\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Proactive systems management</h2><p translate=\"translate\">Red Hat Access Insights is a hosted service designed to help you proactively identify and resolve technical issues in Red Hat Enterprise Linux and Red Hat Cloud Infrastructure environments. Decreasing time spent discovering, researching, and resolving critical issues allows you to focus on driving high value, strategic initiatives.</p></section></div><div class=\"col-md-2 info-image vcenter\"><img src=\"images/Icon_RH_Misc_Lifestyle_RGB_Button.png\" alt=\"Management\"/></div></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-10\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Insights powered by Red Hat</h2><p translate=\"translate\"><strong>The Insights engine </strong>is powered by the unparalleled expertise that comes from over 700 Red Hat Certified Engineers, over 30,000 documented technical solutions, and over 1,000,000 resolved issues. Based on Red Hat’s continuously expanding knowledge base, which includes expertise in third-party hardware and software integration, deterministic rules are constantly being created.</p></section></div></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-2 info-image vcenter\"><img src=\"images/Icon_RH_Software_Data_CMYK_Button.png\" alt=\"Data Collection\"/></div><div class=\"col-md-10 vcenter\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Lightweight data collection</h2><p translate=\"translate\">For analysis by the Insights engine, a minimal subset of information commonly provided in a standard support case is collected by the Insights client. This data is collected via secure protocols at defined intervals then analyzed and discarded at the next upload, or no longer than two weeks later.</p></section></div></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-10 vcenter\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Clear, tailored, and actionable intelligence</h2><p translate=\"translate\">The Insights engine delivers results for review through the Insights UI in Red Hat Satellite and in the Red Hat Customer Portal. Through the intuitive Insights UI, configuration issues discovered during analysis are clearly displayed and prioritized by severity. Tailored remediation steps are presented to help you optimize workloads and confidently resolve critical issues and prevent future issues.</p></section></div><div class=\"col-md-2 info-image vcenter\"><img src=\"images/Icon_RH_Document_Blueprint_RGB_Button.png\" alt=\"Actionable Intelligences\"/></div></div></div></div><div id=\"availability\" class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-12\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Availability</h2><p translate=\"translate\">Red Hat Access Insights is available for current Red Hat customers on Red Hat Enterprise Linux, delivered through either the Customer Portal or Red Hat Satellite. Your current subscription includes Red Hat Access Insights for up to 10 systems. To enroll additional systems, contact <a href=\"http://www.redhat.com/en/about/contact/sales\">sales</a>.</p><div class=\"row\"><div class=\"col-md-6\"><table class=\"table table-cp table-striped table-bordered\"><thead><tr><th translate=\"translate\">Red Hat Access Insights supports</th></tr></thead><tbody><tr><td translate=\"translate\">Red Hat Enterprise Linux 6</td></tr><tr><td translate=\"translate\">Red Hat Enterprise Linux 7</td></tr></tbody></table></div><div class=\"col-md-6\"><table class=\"table table-cp table-striped table-bordered\"><thead><tr><th translate=\"translate\">Delivered via</th></tr></thead><tbody><tr><td translate=\"translate\">Red Hat Customer Portal</td></tr><tr><td translate=\"translate\">Red Hat Satellite 5</td></tr><tr><td translate=\"translate\">Red Hat Satellite 6</td></tr></tbody></table></div></div></section></div></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-12\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Security comes standard</h2><p translate=\"translate\">Like all products and services from Red Hat, Red Hat Access Insights is developed with a focus on security and rigorous deployment controls. </p><p translate=\"translate\"><a ui-sref=\"info.security\">Learn more </a> about how Red Hat Access Insights keeps your data secure while enabling you to proactively manage systems.</p></section></div></div></div></div><div class=\"band band-white band-narrow\"><div class=\"container\"><div class=\"row\"><div class=\"col-md-12\"><section class=\"section\"><h2 translate=\"translate\" class=\"section-title\">Additional resources</h2><div class=\"row\"><div class=\"col-xs-12 col-sm-7 col-md-8 vcenter\"><a href=\"http://www.redhat.com/en/about/press-releases/red-hat-advances-analytics-management-operations-private-public-and-hybrid-clouds\" translate=\"translate\" class=\"btn btn-app cta-link cta-link-sm cta-link-white info-links\">Press Release</a><a href=\"/insights/static/datasheet.pdf\" target=\"_blank\" translate=\"translate\" class=\"btn btn-app cta-link cta-link-sm cta-link-white info-links\">Datasheet</a><a ui-sref=\"info.getting-started.direct\" translate=\"translate\" class=\"btn btn-app cta-link cta-link-sm cta-link-white info-links\">Getting started with Red Hat Access Insights</a><a ui-sref=\"login\" translate=\"translate\" class=\"btn btn-app cta-link cta-link-sm cta-link-white info-links\">Log in to Red Hat Access Insights</a></div><div class=\"col-xs-12 col-sm-5 col-md-4 vcenter\"><a href=\"https://www.youtube.com/watch?v=1tyVrE1KVpg\" target=\"_blank\" class=\"media-thumb\"><span class=\"media-thumb-wrap\"><img src=\"images/keynote-snap.jpg\" alt=\"Keynote Thumbnail\"/></span><span aria-hidden=\"true\" class=\"web-icon-chevron video-thumb-icon\"></span><span class=\"media-duration\">2:12</span></a></div></div></section></div></div></div></div></div>");
