$templateCache.put("js/states/getting-started/views/getting-started-direct.html","<p translate=\"translate\"><strong>Note: </strong>If you participated in the Red Hat Access Insights Beta program, you must follow <a href=\"support/beta/\" target=\"_blank\">these steps</a>.</p><section id=\"install\"><div class=\"step-num\">1</div><h2 translate=\"translate\" class=\"section-header\">Install the Red Hat Access Insights Client and Register Up to 10 Systems for Free</h2><p translate=\"translate\">Register your system for updates with Red Hat Subscription Manager to resolve software dependencies:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"> # subscription-manager register --auto-attach</code></pre><p translate=\"translate\">Install the Red Hat Access Insights RPM:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"> # yum install redhat-access-insights</code></pre></section><section id=\"config\"><div class=\"step-num\">2</div><h2 translate=\"translate\" class=\"section-header\">Configure the Red Hat Access Insights Client</h2><p translate=\"translate\">If you have a web based proxy between your system and the Internet, you can configure the Insights client to connect through it:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># vi /etc/redhat-access-insights/redhat-access-insights.conf</code></pre><pre class=\"code-block code-terminal\"><code># Optional proxy configuration. Example: http://user:pass@192.168.100.50:8080</code><br/><code>proxy=</code><br/></pre></section><section id=\"register\"><div class=\"step-num\">3</div><h2 translate=\"translate\" class=\"section-header\">Register the system to Red Hat Access Insights</h2><p translate=\"translate\"><strong>Note: </strong>Registration with <a href=\"https://access.redhat.com/solutions/11216\" target=\"_blank\">Red Hat Network Classic </a>is also available but requires <a href=\"#classic\" relative-hash=\"relative-hash\" permalink=\"permalink\">additional configuration steps.</a></p><pre class=\"code-block code-terminal\"><code># redhat-access-insights --register</code></pre><p translate=\"translate\">After registration, the Insights client will upload initial system information to Red Hat Access Insights. You should be able to immediately see your system in the <a ui-sref=\"app.systems\">Insights user interface.</a></p><p translate=\"translate\">The initial analysis results will be available shorty thereafter.</p></section><section id=\"classic\"><div class=\"step-num\">4</div><h2 translate=\"translate\" class=\"section-header\">Red Hat Network Classic Instructions</h2><p translate=\"translate\">If you\'ve registered your system with Red Hat Network Classic you\'ll also need to add a username and password:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># vi /etc/redhat-access-insights/redhat-access-insights.conf</code></pre><pre class=\"code-block code-terminal\"><code># Red Hat Customer Portal Credentials</code><br/><code>username=</code><br/><code>password=</code><br/></pre></section>");
$templateCache.put("js/states/getting-started/views/getting-started-sat5.html","<p translate=\"translate\"><strong>Note: </strong>If you participated in the Red Hat Access Insights Beta program, you must follow <a href=\"support/beta/\" target=\"_blank\">these steps</a>.</p><section id=\"prerequisites\"><div class=\"step-num\">1</div><h2 translate=\"translate\" class=\"section-header\">Prerequisites</h2><ul><li translate=\"translate\">Ensure that you\'re running Satellite 5.7.</li><li translate=\"translate\">Install redhat-access-plugin-sat5:</li><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># yum install redhat-access-plugin-sat5</code></pre><li translate=\"translate\">Restart Satellite</li><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># rhn-satellite restart</code></pre><li translate=\"translate\">Satellite Organizations with Access Insights must be running in Connected mode. Any firewalls or proxies through which the Satellite server communicates to Red Hat must allow https communications to <a href=\"https://access.redhat.com\">https://access.redhat.com</a></li></ul></section><section id=\"enable\"><div class=\"step-num\">2</div><h2 translate=\"translate\" class=\"section-header\">Enable Red Hat Access Insights</h2><p translate=\"translate\">Log in to the Satellite Administrator account. Navigate to the Admin --&gt; Insights page. Before enabling Access Insights, click \'Test Conenction to Red Hat Access Insights API\'. If the test is successful you will see a green check circle next to the button. Otherwise you will see a red circle and a log will appear. When the test is successful, select \"Enable Access Insights Service\" then click the \"Update\" button. The Red Hat Access Insights GUI pieces will now appear in Satellite.</p><img src=\"images/img-sat5-step2-admin.png\" class=\"screenshot\"/></section><section id=\"rpm\"><div class=\"step-num\">3</div><h2 translate=\"translate\" class=\"section-header\">Install RPM on Systems</h2><p translate=\"translate\">There are multiple ways to automate this process. See the <a href=\"#register\" relative-hash=\"relative-hash\" permalink=\"permalink\">Register Systems</a> section for details.</p><p translate=\"translate\">Navigate to the Systems --&gt; Insights --&gt; Setup tab as an Organization Administrator. Select the systems to install the redhat-access-insights RPM onto, then click \"Apply\".</p><img src=\"images/img-sat5-step3-setup.png\" class=\"screenshot\"/><p translate=\"translate\">Check the status of the RPM installation by navigating to the Schedule --&gt; Pending Actions page.</p><img src=\"images/img-sat5-step3-pending.png\" class=\"screenshot\"/><p translate=\"translate\">This could take some time depending on your Satellite configuration. <a href=\"https://access.redhat.com/documentation/en-US/Red_Hat_Satellite/5.7/html/Installation_Guide/Enabling_Push_to_Clients.html\">Enabling push to clients</a> will speed up the install. When the RPM is installed you are ready to move on to the next step.</p><img src=\"images/img-sat5-step3-completed.png\" class=\"screenshot\"/></section><section id=\"register\"><div class=\"step-num\">4</div><h2 translate=\"translate\" class=\"section-header\">Register Up to 10 Systems for Free</h2><p translate=\"translate\">To register a system manually:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># redhat-access-insights --register</code></pre><p translate=\"translate\">There are multiple ways to automate this process including Red Hat Satellite\'s built-in <a href=\"https://access.redhat.com/documentation/en-US/Red_Hat_Satellite/5.7/html/Getting_Started_Guide/sect-Managing_Configuration_Channels.html\" target=\"_blank\"> Configuration Management Channels.</a></p><p translate=\"translate\">We\'ve also provided several options for popular configuration management tools below:</p><ul><li><a href=\"https://forge.puppetlabs.com/lphiri/access_insights_client\" target=\"_blank\" translate=\"translate\">Puppet</a></li><li><a href=\"https://supermarket.chef.io/cookbooks/redhat-access-insights-client\" target=\"_blank\" translate=\"translate\">Chef</a></li><li><a href=\"https://galaxy.ansible.com/list#/roles/4098\" target=\"_blank\" translate=\"translate\">Ansible</a></li></ul></section><section id=\"validate\"><div class=\"step-num\">5</div><h2 translate=\"translate\" class=\"section-header\">Validate Data was Reported</h2><p translate=\"translate\">Shortly after you execute `redhat-access-insights --register` on at least one machine, navigate to Systems --&gt; Overview in Satellite. Find the system you registered. The status should either be a green check or a red exclamation point.</p><img src=\"images/img-sat5-step5-systems.png\" class=\"screenshot\"/><p translate=\"translate\">If you see a red exclamation, click it to see the reports about your system.</p><img src=\"images/img-sat5-step5-reports.png\" class=\"screenshot\"/><p translate=\"translate\">To view an overview of all system reports, navigate to Systems --&gt; Insights.</p><img src=\"images/img-sat5-step5-overview.png\" class=\"screenshot\"/></section>");
$templateCache.put("js/states/getting-started/views/getting-started-sat6.html","<p translate=\"translate\"><strong>Note: </strong>If you participated in the Red Hat Access Insights Beta program, you must follow <a href=\"support/beta/\" target=\"_blank\">these steps</a>.</p><section id=\"prerequisites\" class=\"section\"><div class=\"step-num\">1</div><h2 translate=\"translate\" class=\"section-header\">Prerequisites</h2><ul><li translate=\"translate\">Ensure that you’re running the latest Satellite 6.1.</li><li translate=\"translate\">Ensure that you’ve synchronized the latest RHEL 6.7 and RHEL 7.1.z RPMs to your Satellite, and that they are available to your clients.</li><li translate=\"translate\">In order for the Red Hat Satellite Server to be an Access Insights client, please follow the <a ui-sref=\"info.getting-started.direct\">direct </a>registration instructions and run the following command prior to installing the redhat-access-insights client.<pre class=\"code-block code-terminal\"><code class=\"language-shell\"># yum update python-requests</code></pre></li><li translate=\"translate\">A manifest must be downloaded and imported into the Satellite Organization in which Access Insights will be enabled.\nFor more information on managing manifests, see the official Satellite 6<a href=\"https://access.redhat.com/documentation/en-US/Red_Hat_Satellite/6.1/html/User_Guide/sect-Red_Hat_Satellite-User_Guide-Using_Content_Management-Connected_Satellite.html#sect-Red_Hat_Satellite-User_Guide-Connected_Satellite-Using_Red_Hat_Content_Providers\"> documentation.</a></li><li translate=\"translate\">Satellite Organizations with Access Insights must be running in Connected mode. Any firewalls or proxies through which the Satellite server communicates to Red Hat must allow https communications to <a href=\"https://cert-api.access.redhat.com\">https://cert-api.access.redhat.com</a></li></ul></section><section id=\"verifyconnection\" class=\"section\"><div class=\"step-num\">2</div><h2 translate=\"translate\" class=\"section-header\">Verify Connection to Red Hat Access Insights</h2><p translate=\"translate\">Verify that the Satellite server can successfully communicate with Red Hat Access Insights.  In the Satellite UI, navigate to \"Access Insights -&gt; Manage\" and go to the \"Portal Connection\" tab. Verify that the status is \"Connected\" and that the account number displayed is correct for your organization.</p><img src=\"images/img-sat6-step2.jpg\" class=\"screenshot\"/></section><section id=\"register\" class=\"section\"><div class=\"step-num\">3</div><h2 translate=\"translate\" class=\"section-header\">Register Up to 10 Systems for Free</h2><p translate=\"translate\">To register Satellite clients to Red Hat Access Insights, you\'ll need to install the client rpm and then register the system.</p><p translate=\"translate\">Install the RPM:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># yum install redhat-access-insights</code></pre><p translate=\"translate\">Register the system:</p><pre class=\"code-block code-terminal\"><code class=\"language-shell\"># redhat-access-insights --register</code></pre><p translate=\"translate\">If you are using Red Hat Satellite’s configuration management provided by Puppet this process can be automated by applying the preinstalled Puppet class \'access_insights_clients\'. This class can be imported from the Puppet Master into the appropriate Puppet environment and applied to hosts that you wish to subscribe to Red Hat Access Insights.\nFor more information on using on Puppet with Satellite 6, refer to the official <a href=\"https://access.redhat.com/documentation/en-US/Red_Hat_Satellite/6.1/html/User_Guide/chap-Red_Hat_Satellite-User_Guide-Configuring_the_Provisioning_Environment.html#sect-Red_Hat_Satellite-User_Guide-Creating_a_Host_Group\">documentation.</a></p></section><section id=\"viewingandmanaging\" class=\"section\"><div class=\"step-num\">4</div><h2 translate=\"translate\" class=\"section-header\">Viewing Reports and Managing Access From the Satellite UI</h2><p translate=\"translate\" class=\"margin-top\">To view Access Insights reports, navigate to \"Access Insights -&gt; Overview\" in the Satellite 6 UI.</p><img src=\"images/img-sat6-step5a.jpg\" class=\"screenshot\"/><p translate=\"translate\" class=\"margin-top\">To view and manage Access Insights rules, navigate to \"Access Insights -&gt; Rules\".</p><img src=\"images/img-sat6-step5b.jpg\" class=\"screenshot\"/><p translate=\"translate\" class=\"margin-top\">To view the list of systems that are reporting data to Red Hat Access Insights, navigate to \"Access Insights -&gt; Systems\".</p><img src=\"images/img-sat6-step5c.jpg\" class=\"screenshot\"/><p translate=\"translate\" class=\"margin-top\">To view connection status or enable/disable Red Hat Access Insights globally for an Organization, navigate to \"Access Insights -&gt; Manage\".</p><img src=\"images/img-sat6-step5d.jpg\" class=\"screenshot\"/></section><section id=\"userroles\" class=\"section\"><div class=\"step-num\">5</div><h2 translate=\"translate\" class=\"section-header\">Assign Red Hat Access Insights Roles to Users</h2><p translate=\"translate\">By default, only administrators can view Access Insights Report and Configuration screens. You can give users who are not administrators access to the Access Insights screens by assigning them the appropriate roles as follows.</p><ul><li translate=\"translate\">To give users access to all Access Insights screens, including the \"Manage\' screen, assign them the \"Access Insights Admin\" role.</li><li translate=\"translate\">To give users Access Insights report and rule viewing access, assign them the \"Access Insights Viewer\" role.</li></ul><p translate=\"translate\">For more information on roles refer to the official Satellite 6 <a href=\"https://access.redhat.com/documentation/en-US/Red_Hat_Satellite/6.1/html/User_Guide/chap-Red_Hat_Satellite-User_Guide-Users_and_Roles.html\">documentation.</a></p></section>");
